import requireurl from '../../requist/requist.js'
import uniPopup from "../../components/uni-popup/uni-popup.vue"
import uniCountdown from "@/components/uni-countdown/uni-countdown.vue"
export default {
  components: {uniCountdown,uniPopup},
	data(){
		return{
			title: 'map',
			latitude: 0,
			longitude: 0,
			covers: [],
			mapObj:{},
			Mapheight:0,
			minute:0,
			sec:20,
			show: false,
			type:'center',
			callphone:false,
			usermoblie:'',
			yuyue:true,
			yuyuedata:{},
			timer:null,
			codestatus:false,
			CodeMessage:''
			
		}
	},
	onShow() {
		uni.getLocation({
		    type: 'gcj02', //返回可以用于uni.openLocation的经纬度
		    success:  (res)=> {
				console.log('用户允许')
				console.log(res)
				this.$nextTick(()=>{
					this.latitude = res.latitude
					this.longitude = res.longitude
					uni.setStorage({
					    key: 'storage_latitude',
					    data:  res.latitude,
					    success: function () {
							uni.setStorage({
								    key: 'storage_longitude',
								    data:  res.longitude,
							});
					    }
					});
				})
				requireurl.getData(`/applet/search/outlets/?location=${res.longitude},${res.latitude}&search=&radius=&action=near`,this.getwangadd,this.file)
			},
			fail :(err)=> {
				uni.removeStorage({
				    key: 'storage_latitude',
				    success: function (res) {
						uni.removeStorage({
						    key: 'storage_longitude',
						    success: function (res) {
								console.log('用户拒绝授权，清除位置')
						    }
						});
				    }
				});
				
			}
		});
		
		
		
		if(uni.getStorageSync('storage_user').token){
			requireurl.getData(`/applet/has/appointment/order/`,this.getorder,this.fileorder)
		}else{
			this.yuyue = -1
		}
	},
	methods:{
		//拖动地图请求数据-
		aaaaaaa(e){
			console.log(e)
			if(e.type === 'end'){
				if(this.timer){
					console.log('清除')
					clearTimeout(this.timer)
				}
				console.log(e)
				//延迟请求减小服务器压力
				this.timer = setTimeout(()=>{
					console.log('开始')
					var a = uni.createMapContext('mapId',this)
					// 获取当前地图中心位置
					a.getCenterLocation({
						success:(res)=>{
							console.log(res)
							requireurl.getData(`/applet/search/outlets/?location=${res.longitude},${res.latitude}&search=&radius=&action=near`,this.getwangadd,this.file)
						},
						fail:()=>{
							console.log('失败')
						}
					})
				},1000)
			}
		},
		
		//点击地图标注网点
		clickmarkertap(e){
			console.log('我点击了icon')
			console.log(e)
			
			uni.navigateTo({
			    url: `../netdetails/netdetails?id=${e.markerId}`
			});
		},
		//地图更新完成之后
		asdasdasd(e){
			console.log('渲染完成')
				
		},
		
		
		//点击回到当前定位位置
		clickgoaddress(){
			uni.getLocation({
			    type: 'gcj02', //返回可以用于uni.openLocation的经纬度
			    success:  (res)=> {
					var a = uni.createMapContext('mapId',this)
					a.moveToLocation({
						latitude : res.latitude,
						longitude : res.longitude
					})
					this.latitude =  res.latitude,
					this.longitude =  res.longitude
			    }
			});
			
		},
		
		//进入我的界面
		clickgohome(){
			if(uni.getStorageSync('storage_user').token){
				uni.navigateTo({
				    url: '../usermiddle/usermiddle'
				});
			}else{
				uni.navigateTo({
				    url: '../selectlogin/selectlogin'
				});
			}
			
		},
		
		
		//弹出客服框
		showservice(){
			this.callphone = true
		},
		
		
		//拨打电话
		callmeoblie(){
			var moblie = this.usermoblie
			wx.makePhoneCall({
			  phoneNumber: moblie,
			})
		},
		
		//倒计时结束回调
		timeup(){
			console.log("结束了")
			this.yuyue = -1
		},
		
		//打开  已被预约提醒  + 暂未认证提醒
		quedingnle(){
			this.show = true
			
		},
		//关闭  已被预约提醒  + 暂未认证提醒
		cancel(){
			this.show=false
			let obj = {
				id:this.yuyuedata.id
			}
			requireurl.getPutData(`/applet/cancel/order/`,obj,this.clearorder,this.file)
			
			// uni.showToast({
			//     title: '取消成功',
			// 	icon :'none',
			//     duration: 1500
			// });
		},
		
		//取消订单回调
		clearorder(e){
			if(e.statusCode === 200){
				this.yuyue = -1
				uni.showToast({
				    title: '取消成功',
					icon :'none',
				    duration: 1500
				});
				
				
				requireurl.getData(`/applet/search/outlets/?location=${this.longitude},${this.latitude}&search=&radius=&action=near`,this.getwangadd,this.file)
			}
		},
		
		//点击进入扫一扫
		gosao(){
			// uni.navigateTo({
			//     url: '../home/home'
			// });
			if(!uni.getStorageSync('storage_user').token){
				uni.navigateTo({
				    url: '../selectlogin/selectlogin'
				});
				return
			}
			wx.scanCode(
			{
				onlyFromCamera:true,
			     success: (res) => {
					 uni.showLoading({
					     title: '正在检测电池状态',
					 	mask:true
					 });
					 console.log(res)
					 let numbers = res.result.split('?')[1].split('=')[1]
					 requireurl.getData(`/applet/scan/?outlet_number=${numbers}`,this.jiancode,this.file)
					 
					
			     },
				 fail:(err)=>{
				 	console.log(err)
				 },
				 complete:()=>{
				 	console.log('执行了')
				 }
				 				
			},
				
			
			
			)
		},
		//校检成功回调
		jiancode(e){
			console.log('这里......')
			console.log(e)
				uni.hideToast();
			if(e.statusCode === 200){
				if(e.data.success){
					uni.navigateTo({
					    url: '../taskdian/taskdian'
					});
				}else{
					this.codestatus = true
					this.CodeMessage = e.data.message
				}
			}else{
				this.codestatus = true
				this.CodeMessage = e.data.err
			}
		
		},
		
		//校检失败
		file(err){
			uni.hideToast();
		},
		
		//进入网点搜索
		searchaddress(){
			uni.navigateTo({
			    url: '../searchaddress/searchaddress'
			});
		},
		
		//客服手机号回调
		mobilekf(e){
			if(e.statusCode === 200){
				this.usermoblie = e.data.message
			}else{
				uni.showToast({
				    title: '请求错误',
					icon :'none',
				    duration: 1500
				});
			}
		},
		
		//获取附近网点
		getwangadd(e){
			if(e.statusCode === 200){
				console.log("执行")
				this.covers = []
				console.log(e)
				this.covers = e.data
			}else{
				uni.showToast({
				    title: '获取网点失败',
					icon :'none',
				    duration: 1500
				});
			}
		},
		
		//查询订单是否被预约
		getorder(e){
			if(e.statusCode === 200){
				this.yuyue = e.data.has_appointment_order
				if(e.data.has_appointment_order === 0){
					this.yuyuedata = e.data.data
					this.minute = parseInt(e.data.data.left_time/60)
					this.sec = e.data.data.left_time%60
				}
			}else{
				this.yuyue = -1
			}
		},
		
		
		fileorder(){
			console.log('错误')
		}
	
	},
	mounted() {
	
		
		 requireurl.getData(`/applet/service_tel/`,this.mobilekf,this.file)
		//创建节点选择器
		const query = wx.createSelectorQuery()
		    query.select('#test').boundingClientRect()
		    query.selectViewport().scrollOffset()
		    query.exec( (res) =>{
				this.Mapheight = res[0].height
		    })
	}
}
